const SHOW_NOTIFY = 'SHOW_NOTIFY';
const HIDE_NOTIFY = 'HIDE_NOTIFY';
const state = {
    show: false,
    options: {}
};
const mutations = {
    [HIDE_NOTIFY](state) {
        state.show = false;
    },
    [SHOW_NOTIFY](state, options) {
        state.show = true;
        state.options = options;
    }
};
const getters = {
    show: (state) => {
        return state.show;
    },
    options: (state) => {
        return state.options;
    }
};
const actions = {
    showNotify: ({ state, commit, rootState }, options) => {
        commit(SHOW_NOTIFY, options);
    },
    hideNotify: ({ state, commit, rootState }) => {
        commit(HIDE_NOTIFY);
    }
};
export default {
    state,
    mutations,
    getters,
    actions
};



