const ADD_ANSWER = 'ADD_ANSWER';
const SET_LASTLETTER = 'SET_LASTLETTER';
;
export const ANSWER_TYPES = {
    computer: 101,
    user: 102
};
export class Answer {
}
;
const state = {
    //full answer with code, country, date
    answers: [],
    lastLetter: ''
};
const mutations = {
    [ADD_ANSWER](state, answer) {
        state.answers.unshift(answer);
    },
    [SET_LASTLETTER](state, letter) {
        state.lastLetter = letter;
    }
};
const getters = {
    answers: (state) => {
        return state.answers;
    },
    lastLetter: (state) => {
        return state.lastLetter;
    }
};
const actions = {
    addAnswer: ({ state, commit, rootState }, answer) => {
        commit(ADD_ANSWER, answer);
    },
    setLastLetter: ({ state, commit, rootState }, letter) => {
        commit(SET_LASTLETTER, letter);
    }
};
export default {
    state,
    mutations,
    getters,
    actions
};



